@import "utils/variables";

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;

  height: 60px;
  padding: 0 50px;

  box-shadow: 0 2px 4px $color-grey-shadow;

  &__logo {
    height: 40px;
  }
}

.nav {
  &__list {
    display: flex;
    margin: 0;
    padding: 0;
  }

  &__item {
    position: relative;
    color: $color-main-accent;
    list-style: none;
    line-height: 60px;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__link {
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;

    transition: color $transition-duration;

    &:hover {
      color: $color-blue-accent;
    }
  }
}
